		background-color: rgb(202, 202, 202) !important;
		width: 60% !important;
		height: 40px;
		margin: 0px auto;
		border-radius: 50px;
		box-shadow: none !important;
	}

	.tab_theame button {
		flex: 1;
		padding: 0px !important;
		min-width: 20px !important;
		min-height: 40px !important;
		z-index: 1;
		color: #fff;
	}
	.tab_theame button:hover {
		background-color: var(--bg_color);
		color: #fff;
		border-bottom: none !important;
		border-radius: 50px;
		transition: 0.3s ease-in;
	}

	.PrivateTabIndicator-colorSecondary-4 {
		min-width: 20px;
		min-height: 40px;
		background-color: var(--bg_color) !important;
		border-radius: 50px;
		top: 0;
	}

	.tab_theame .button.active {
		background-color: var(--bg_color) !important;
	}

	.tab-content {
		width: 100%;
		height: 60vh;
		margin: 0px auto;
	}
	.leftSide_card {
		width: 100%;
	}

	.rightSide_card {
		width: 100%;
	}
}

